                  class="text-body1 font-medium text-primary-500 col-12 flex items-center q-pb-md"
                >
                  Uploaded Fonts
                  <q-badge
                    class="q-ml-sm"
                    color="accent"
                    text-color="secondary"
                    align="middle"
                    >{{ reactiveDisplayBrand.fonts?.length }}</q-badge
                  >
                </div>
                <div
                  class="row items-center justify-start q-col-gutter-sm scroll"
                  style="max-height: 340px"
                >
                  <div
                    v-for="file in reactiveDisplayBrand.fonts"
                    :key="file"
                    class="col-12 col-sm-6 col-md-3 rounded overflow-hidden"
                  >
                    <div class="fit hover-visibility">
                      <a
                        target="_blank"
                        :href="file.download_url"
                        @click.prevent
                        class="row items-center q-col-gutter-sm text-primary"
                        :class="{
                          [`cursor-${
                            file.isSkeleton ? 'not-allowed' : 'pointer'
                          }`]: true,
                        }"
                      >
                        <div
                          class="col-3"
                          @click.prevent="
                            file.isSkeleton
                              ? void 0
                              : $downloadItem(file.download_url, file.name)
                          "
                        >
                          <CardIconBox
                            :useSpinner="file.isSkeleton"
                            :icon="
                              file.isSkeleton
                                ? 'icon-upload-cloud-01'
                                : 'icon-type-square'
                            "
                          />
                        </div>
                        <div
                          class="hover-text-secondary overflow-hidden"
                          :class="{
                            ['text-primary-700']: file.isSkeleton,
                            [`col${editing ? '-7' : ''}`]: true,
                          }"
                          style="text-overflow: ellipsis"
                          @click.prevent="
                            file.isSkeleton
                              ? void 0
                              : $downloadItem(file.download_url, file.name)
                          "
                        >
                          {{ file.name }}
                        </div>
                        <div
                          v-show="editing"
                          class="row q-col-gutter-x-sm justify-end"
                          :class="{ [`col${editing ? '-2' : 'auto'}`]: true }"
                        >
                          <q-icon
                            v-if="editing"
                            flat
                            round
                            size="xs"
                            name="icon-dots-horizontal"
                            class="all-pointer-events text-primary-500"
                            @click.stop
                            @click.prevent
                          >
                            <q-menu auto-close>
                              <q-list separator>
                                <q-item
                                  v-if="!file.isSkeleton"
                                  clickable
                                  class="hover-text-secondary"
                                  :disable="
                                    (removingFile?.id &&
                                      file.id === removingFile?.id) ||
                                    (removingFile?.__key &&
                                      file.file_info?.__key ===
                                        removingFile?.__key)
                                  "
                                  @click.prevent="
                                    $downloadItem(file.download_url, file.name)
                                  "
                                >
                                  <q-item-section side class="q-pr-sm">
                                    <q-icon name="icon-download-02" size="xs" />
                                  </q-item-section>
                                  <q-item-section>Download</q-item-section>
                                </q-item>
                                <q-item
                                  v-if="!file.isSkeleton"
                                  clickable
                                  class="hover-text-secondary"
                                  :disable="
                                    (removingFile?.id &&
                                      file.id === removingFile?.id) ||
                                    (removingFile?.__key &&
                                      file.file_info?.__key ===
                                        removingFile?.__key)
                                  "
                                  @click.prevent="
                                    (e) => {
                                      fileToRename = { file, target: 'fonts' };
                                      openRenameDialog = true;
                                      e.preventDefault();
                                    }
                                  "
                                >
                                  <q-item-section side class="q-pr-sm">
                                    <q-icon name="icon-edit-03" size="xs" />
                                  </q-item-section>
                                  <q-item-section>Rename</q-item-section>
                                </q-item>
                                <q-item
                                  clickable
                                  class="text-negative hover-text-negative"
                                  :disable="
                                    !editing ||
                                    (removingFile?.id &&
                                      file.id === removingFile?.id) ||
                                    (removingFile?.__key &&
                                      file.file_info?.__key ===
                                        removingFile?.__key)
                                  "
                                  @click.prevent="
                                    (e) => {
                                      fileToDelete = [file, 'fonts'];
                                      openDeleteDialog = true;
                                      e.preventDefault();
                                    }
                                  "
                                >
                                  <q-item-section side class="q-pr-sm">
                                    <q-icon
                                      name="icon-trash-01"
                                      size="xs"
                                      color="negative"
                                    />
                                  </q-item-section>
                                  <q-item-section>Delete</q-item-section>
                                </q-item>
                              </q-list>
                            </q-menu>
                          </q-icon>
                        </div>
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </q-tab-panel>

          <q-tab-panel name="graphics" class="q-pa-none">
            <div
              v-if="
                !isBrandArchived &&
                (editing || !reactiveDisplayBrand.graphics?.length)
              "
              class="q-pb-md"
            >
              <FileUploader
                ref="graphics"
                max-file-size="10000000"
                :factory="(files) => factoryFn(files, 'graphics')"
                :note="
                  editing || reactiveDisplayBrand.graphics?.length
                    ? 'AI, EPS, PSD, SVG, PNG, GIF or JPEG, FIG, XD (max. 10MB)'
                    : 'No graphic elements added.'
                "
                accept=".png, .ico, .jpg, .jpeg, .svg, .ai, .psd, .eps, .fig, .xd"
                :removedFile="removedFile"
                @added="
                  (files) => {
                    if (!editing) editing = true;
                    addUploadToModel(
                      'brand_uploads',
                      'graphics',
                      files,
                      'graphics'
                    );
                    addTempSkeleton(files, 'graphics');
                  }
                "
                thumbnail="icon-image-03"
                @uploaded="(e) => uploadedEvent(e, 'graphics')"
                @failed="(e) => failedEvent(e, 'graphics')"
              />
            </div>
            <div
              v-if="reactiveDisplayBrand.graphics?.length"
              class="row items-center justify-start q-col-gutter-md scroll"
              :class="{ ['q-mt-sm']: editing }"
              style="max-height: 670px"
            >
              <div
                v-for="file in reactiveDisplayBrand.graphics"
                :key="file"
                class="col-6 col-sm-6 col-md-4 rounded overflow-hidden"
              >
                <ImageLoader
                  :file="file"
                  :editing="editing"
                  :removingFile="removingFile"
                  :stared="brandAvatar?.id === file.id"
                  @renamed="(e) => fileRenameUpdate(e, 'graphics')"
                  @delete="
                    (res_file) => {
                      fileToDelete = [res_file, 'graphics'];
                      openDeleteDialog = true;
                    }
                  "
                />
              </div>
            </div>
          </q-tab-panel>

          <q-tab-panel name="icons" class="q-pa-none">
            <div
              v-if="
                !isBrandArchived &&
                (editing || !reactiveDisplayBrand.icons?.length)
              "
              class="q-pb-md"
            >
              <FileUploader
                ref="icons"
                max-file-size="10000000"
                :factory="(files) => factoryFn(files, 'icons')"
                :note="
                  editing || reactiveDisplayBrand.icons?.length
                    ? 'SVG, PNG, JPEG, ICO or GIF (max. 10MB)'
                    : 'No brand icons added.'
                "
                accept=".svg, .png, .jpeg, .jpg, .gif"
                :removedFile="removedFile"
                @added="
                  (files) => {
                    if (!editing) editing = true;
                    addUploadToModel('brand_uploads', 'icons', files, 'icons');
                    addTempSkeleton(files, 'icons');
                  }
                "
                thumbnail="icon-image-03"
                @uploaded="(e) => uploadedEvent(e, 'icons')"
                @failed="(e) => failedEvent(e, 'icons')"
              />
            </div>
            <div
              v-if="reactiveDisplayBrand.icons?.length"
              class="row items-center justify-start q-col-gutter-sm scroll"
              :class="{ ['q-mt-sm']: editing }"
              style="max-height: 670px"
            >
              <div
                v-for="file in reactiveDisplayBrand.icons"
                :key="file"
                class="col-6 col-sm-3 col-md-2 rounded overflow-hidden"
              >
                <ImageLoader
                  noTitle
                  :file="file"
                  :editing="editing"
                  :removingFile="removingFile"
                  :stared="brandAvatar?.id === file.id"
                  class="bg-accent2"
                  img-class="q-pa-sm"
                  optionSize="xs"
                  @renamed="(e) => fileRenameUpdate(e, 'icons')"
                  @delete="
                    (res_file) => {
                      fileToDelete = [res_file, 'icons'];
                      openDeleteDialog = true;
                    }
                  "
                />
              </div>
            </div>
          </q-tab-panel>

          <q-tab-panel name="images" class="q-pa-none">
            <div
              v-if="
                !isBrandArchived &&
                (editing || !reactiveDisplayBrand.images?.length)
              "
              class="q-pb-md"
            >
              <FileUploader
                ref="images"
                max-file-size="10000000"
                :factory="(files) => factoryFn(files, 'images')"
                :note="
                  editing || reactiveDisplayBrand.images?.length
                    ? 'SVG, PNG, JPEG, ICO or GIF (max. 10MB)'
                    : 'No brand images added.'
                "
                accept=".svg, .png, .jpeg, .jpg, .gif"
                :removedFile="removedFile"
                @added="
                  (files) => {
                    if (!editing) editing = true;
                    addUploadToModel(
                      'brand_uploads',
                      'images',
                      files,
                      'images'
                    );
                    addTempSkeleton(files, 'images');
                  }
                "
                thumbnail="icon-image-03"
                @uploaded="(e) => uploadedEvent(e, 'images')"
                @failed="(e) => failedEvent(e, 'images')"
              />
            </div>
            <div
              v-if="reactiveDisplayBrand.images?.length"
              class="row items-center justify-start q-col-gutter-md scroll"
              :class="{ ['q-mt-sm']: editing }"
              style="max-height: 670px"
            >
              <div
                v-for="file in reactiveDisplayBrand.images"
                :key="file"
                class="col-6 col-sm-6 col-md-4 rounded overflow-hidden"
              >
                <ImageLoader
                  :file="file"
                  :editing="editing"
                  :removingFile="removingFile"
                  :stared="brandAvatar?.id === file.id"
                  @renamed="(e) => fileRenameUpdate(e, 'images')"
                  @delete="
                    (res_file) => {
                      fileToDelete = [res_file, 'images'];
                      openDeleteDialog = true;
                    }
                  "
                />
              </div>
            </div>
          </q-tab-panel>

          <q-tab-panel name="social_accounts" class="q-pa-none">
            <template v-if="!editing">
              <div
                v-if="Object.values(brandInfo.social_accounts || {})?.length"
                class="q-col-gutter-md"
              >
                <template
                  v-for="social in Object.keys(
                    brandInfo.social_accounts || {}
                  )?.sort?.((a, b) =>
                    a.toLowerCase() > b.toLowerCase()
                      ? 1
                      : a.toLowerCase() < b.toLowerCase()
                      ? -1
                      : 0
                  )"
                  :key="social"
                >
                  <div
                    v-if="brandInfo.social_accounts?.[social]"
                    class="row items-center justify-start q-col-gutter-xs"
                  >
                    <div class="col-12 col-sm-3 font-medium text-primary-700">
                      <div class="row items-center q-gutter-x-sm">
                        <q-icon
                          v-if="false"
                          :name="formatMediaPlatformIconSrc(social)"
                          size="xs"
                          class="self-end"
                        />
                        <span>
                          {{ capitalize(social) }}
                        </span>
                      </div>
                    </div>
                    <div class="col-12 col-sm-9">
                      <external-link
                        :url="
                          (getSocialInfo(social)?.base_url || '') +
                          '/' +
                          brandInfo.social_accounts?.[social]
                        "
                        :icon="formatMediaPlatformIconSrc(social)"
                      />
                    </div>
                  </div>
                </template>
              </div>
              <div v-else>
                <span class="font-medium"> No social media accounts. </span>
              </div>
            </template>
            <div
              v-else
              class="row items-center justify-start q-col-gutter-xs q-col-gutter-sm"
            >
              <template
                v-for="opt in Object.values(socialMedia || {})?.sort?.((a, b) =>
                  a.platform?.toLowerCase() > b.platform?.toLowerCase()
                    ? 1
                    : a.platform?.toLowerCase() < b.platform?.toLowerCase()
                    ? -1
                    : 0
                )"
                :key="opt.platform"
              >
                <div class="col-12 col-sm-3 font-medium text-primary-700">
                  <div class="row items-center q-gutter-x-sm">
                    <q-icon
                      v-if="false"
                      :name="formatMediaPlatformIconSrc(opt.platform)"
                      size="xs"
                      class="self-end"
                    />
                    <span>
                      {{ capitalize(opt.platform) }}
                    </span>
                  </div>
                </div>
                <div class="col-12 col-sm-9">
                  <q-input
                    dense
                    outlined
                    placeholder="username"
                    v-model="brand.social_accounts[opt.platform]"
                    :style="{
                      ['max-width']: $q.screen.lt.sm ? '100%' : '400px',
                    }"
                  >
                    <template v-slot:prepend>
                      <div class="text-primary-700 text-body2">
                        {{
                          (getSocialInfo(opt.platform)?.base_url || '')
                            .replace('http://', '')
                            .replace('https://', '')
                            .replace('www.', '')
                        }}{{ !opt.platform.endsWith('/') ? '/' : '' }}
                      </div>
                      <q-separator vertical class="q-ml-sm" />
                    </template>
                  </q-input>
                </div>
              </template>
            </div>
          </q-tab-panel>
        </q-tab-panels>
      </q-card-section>
    </q-card>

    <TaskProjectRequests nofilter :brand_id="brand_id" class="q-my-lg" />

    <!-- Rename File Dialog -->
    <q-dialog v-model="openRenameDialog">
      <RenameFolderFile
        :rename="fileToRename.file"
        @close="openRenameDialog = false"
        @success="(e) => fileRenameUpdate(e, fileToRename.target)"
      />
    </q-dialog>

    <!-- Delete Dialog -->
    <q-dialog v-model="openDeleteDialog">
      <dynamic-dialog-confirmation
        v-bind="{
          ...$root.messagesDialogs.confirmDelete,
          messageTitle: `Confirm to delete ${fileToDelete?.[0]?.name}?`,
        }"
        @okay="deleteFileAction(fileToDelete?.[0], fileToDelete?.[1])"
      />
    </q-dialog>

    <image-lightbox
      :visible="showLightBox"
      :imgs="lightboxImages"
      :index="lightboxIndex"
      @hide="showLightBox = false"
    />
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import useHelpers from 'stores/helpers';
import { format, openURL } from 'quasar';
import {
  ColorThief,
  getImageFromBase64str,
  getImageStringFromURL,
  objectCopy,
  checkIfObject,
  avatarNameAbbr,
  extractErrorMessageString,
} from 'src/assets/scripts/functions';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import ColorPickerCard from 'src/components/Helpers/ColorPickerCard.vue';
import SelectBrandType from 'src/components/Helpers/SelectBrandType.vue';
import FileUploader from 'src/components/Helpers/FilesUploader.vue';
import ImageLoader from 'src/components/Helpers/ImageLoader.vue';
import { FileUploader_Mixin, Helpers_Mixin } from 'src/mixins';
import fnStore from 'src/stores/fn.store';
import TaskProjectRequests from 'src/components/Requests/TaskProjectRequests.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import TextArea1 from 'src/components/Helpers/TextArea1.vue';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import RenameFolderFile from 'src/components/Dialogs/RenameFolderFile.vue';
import SelectFont from 'src/components/Helpers/SelectFont.vue';
import DisplayString from 'src/components/Helpers/Format/DisplayString.vue';

export default defineComponent({
  name: 'BrandOverview',

  mixins: [FileUploader_Mixin, Helpers_Mixin],

  components: {
    TaskProjectRequests,
    CardIconBox,
    ColorPickerCard,
    SelectBrandType,
    FileUploader,
    ImageLoader,
    InputWebsite,
    TextArea1,
    DynamicButton,
    RenameFolderFile,
    SelectFont,
    DisplayString,
  },

  data() {
    return {
      title: 'Brand Overview',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      brandTab: 'general',
      removedFile: null,
      removingFile: {},
      brand: {},
      brand_uploads: {},
      openURL,
      avatarNameAbbr,
      capitalize: format.capitalize,
      editing: false,
      savingChanges: false,
      colorPalette: undefined,
      brandColorSrc: '',
      brandColorSrcBlob: '',
      brandColorSrcFile: null,
      brandColorSrcDelete: null,
      validImageForColorPalette: false,
      fileToDelete: null,
      openDeleteDialog: false,
      fileToRename: {},
      openRenameDialog: false,
      localBrandInfo: {},
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.getBrandInfo();
    if (this.route_tab) this.brandTab = this.route_tab;
  },

  computed: {
    ...mapState(useHelpers, ['socialMedia']),
    ...mapState(useUser, ['selectedOrg', 'selectedOrgBrands']),
    ...mapState(useGrowmodo, [
      'brandCategories',
      'taskDirectory',
      'taskDirectory_Categories',
      'projectDirectory',
      'projectDirectory_Categories',
    ]),

    isBrandArchived() {
      return this.brandInfo.deleted_at ? true : false;
    },

    route_tab() {
      return this.$route.query.tab || '';
    },

    brand_id() {
      const id = this.$route.params?.brandId;
      return isNaN(id) ? 0 : Number(id);
    },

    brandAvatar() {
      return this.brandInfo.avatar || {};
    },

    brandInfo() {
      return this.selectedOrgBrands[this.brand_id] || this.localBrandInfo;
    },

    reactiveDisplayBrand() {
      return (
        (this.isBrandArchived
          ? this.localBrandInfo
          : this.editing
          ? this.brand
          : this.brandInfo) || {}
      );
    },

    currentBrandColors() {
      return Object.keys(this.reactiveDisplayBrand.colors || {});
    },

    brandTabEditLabel() {
      switch (this.brandTab) {
        case 'general':
          return 'Edit Info';

        case 'logo':
          return 'Edit Logos';

        case 'colors':
          return 'Edit Colors';

        case 'fonts':
          return 'Edit Fonts';

        case 'graphics':
          return 'Edit Graphic Elements';

        case 'icons':
          return 'Edit Icons';

        case 'images':
          return 'Edit Images';

        default:
          return 'Edit Info';
      }
    },
  },

  watch: {
    openRenameDialog: {
      async handler(val) {
        console.warn('openRenameDialog', val);
      },
    },
    brand: {
      async handler(val) {
        this.updateLightBox();
      },
      deep: true,
    },
    editing: {
      async handler(val) {
        this.updateLocalBrand();
        this.updateLightBox();
      },
    },
    brandTab: {
      async handler(val) {
        if (!this.editing) this.updateLocalBrand();
        else this.editing = false;
        this.updateLightBox();
      },
    },
    brandColorSrcBlob: {
      async handler(val) {
        if (val) {
          let encodedImage = await getImageStringFromURL(val, false);
          if (encodedImage) {
            this.brandColorSrc = encodedImage;
            this.validImageForColorPalette = true;
            setTimeout(this.getColorPalette, 500);
          } else {
            this.validImageForColorPalette = false;
          }
        } else {
          this.validImageForColorPalette = false;
        }
      },
    },
  },

  methods: {
    ...mapActions(useHelpers, [
      'deleteFile',
      'getSocialInfo',
      'formatMediaPlatformIconSrc',
    ]),
    ...mapActions(useUser, [
      'getSingleBrand',
      'updateBrand',
      'updateBrandColors',
      'updateBrandSocial',
      'updateBrandGoogleFonts',
      'removeBrandFile',
      'updateOrgBrand',
      'updateBrandAvatar',
    ]),

    addUploadToTargetBrand(data, target_key) {
      this.updateOrgBrand(
        this.brandInfo?.id,
        data,
        { key: target_key },
        this.selectedOrg?.id
      );
    },

    getBrandInfo() {
      this.getSingleBrand(this.brand_id)
        .then((res) => {
          if (res?.success) {
            this.localBrandInfo = res.data || {};
          }
        })
        .finally(() => {
          this.updateLightBox();
          this.updateLocalBrand();
        });
    },

    updateLocalBrand() {
      const clonedBrand = objectCopy(this.brandInfo);
      if (!checkIfObject(clonedBrand.social_accounts))
        clonedBrand.social_accounts = {};
      if (!checkIfObject(clonedBrand.colors)) clonedBrand.colors = {};
      if (!Array.isArray(clonedBrand.googlefonts)) clonedBrand.googlefonts = [];
      this.brand = clonedBrand;
    },

    async saveBrandChanges() {
      this.savingChanges = true;
      const _ = this.brand || {};
      const tab = this.brandTab;
      let response;
      if (tab === 'general') {
        // Brand General Information
        const payload = {
          brand_name: _.brand_name,
          brand_type: _.brand_type,
          tagline: _.tagline,
          description: _.description,
          target_audience: _.target_audience,
          competitors: _.competitors,
          value_proposition: _.value_proposition,
        };
        if (_.website) payload.website = _.website;
        // Brand Social Media Accounts
        if (
          checkIfObject(_.social_accounts) &&
          Object.keys(_.social_accounts).length
        ) {
          if (_.social_accounts) payload.social_accounts = {};
          for (const s of Object.keys(_.social_accounts)) {
            if (_.social_accounts[s])
              payload.social_accounts[s] = _.social_accounts[s];
          }
        }
        response = await this.updateBrand(payload, this.brand_id);
      } else if (tab === 'colors') {
        // Brand Colors
        const allColors = {};
        if (Object.keys(_.colors)?.length)
          for (const c of Object.keys(_.colors).sort((a, b) =>
            a.toLowerCase() > b.toLowerCase()
              ? 1
              : a.toLowerCase() < b.toLowerCase()
              ? -1
              : 0
          )) {
            if (!_.colors[c]) continue;
            allColors[c] = _.colors[c];
          }
        response = await this.updateBrandColors(
          {
            colors: allColors,
          },
          this.brand_id
        );
      } else if (tab === 'social_accounts') {
        // Brand Social Media Accounts
        const social_accounts = {};
        for (const s of Object.keys(_.social_accounts)) {
          if (_.social_accounts[s]) social_accounts[s] = _.social_accounts[s];
        }
        response = await this.updateBrandSocial(
          { social_accounts },
          this.brand_id
        );
      } else {
        if (tab === 'fonts') {
          // Brand Fonts from Google
          const payload = {
            googlefonts: _.googlefonts,
          };
          response = await this.updateBrandGoogleFonts(payload, this.brand_id);
        }
        // Brand Uploads
        // Check files if no uploading
        for (const file of _?.[tab]?.files || []) {
          if (file.__status === 'uploading') {
            response = {
              success: false,
              message: 'Upload is still in progress',
            };
            break;
          }
        }
        if (!response) response = { success: true };
      }
      const message = extractErrorMessageString(response?.message);
      this.savingChanges = false;
      if (response?.success) {
        if (message) {
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: message,
          });
        }
        this.editing = false;
        return true;
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error while processing your request',
        });
        return false;
      }
    },

    async deleteFileAction(file, target) {
      if (!this.brand[target]) return;
      this.removingFile = file;
      let response = await this.deleteFile(file.id);
      if (file?.id && file?.id === this.brandAvatar?.id) {
        // Delete Brand Avatar
        this.updateBrandAvatarAction(null, false);
      }
      if (file.isSkeleton || response?.success) {
        let delkey, indexToRemove;
        this.removeBrandFile(this.brand_id, file, target, this.selectedOrg?.id);
        if (file.isSkeleton) {
          // Delete skeleton
          indexToRemove = this.brand[target].findIndex(
            (e) => e.file_info?.__key === file.file_info?.__key
          );
          this.removedFile = file.file_info;
          delkey = file.file_info?.__key;
        } else {
          // Delete file from edit copy
          indexToRemove = this.brand[target].findIndex((e) => e.id === file.id);
          if (this.brand_uploads[target]) {
            // Newly Uploaded file Delete from Model
            const fileIndex = this.brand_uploads[target].files.findIndex(
              (e) => e.name === file.name
            );
            const u_file = this.brand_uploads[target].files[fileIndex];
            this.removedFile = u_file;
            delkey = u_file?.__key;
          }
        }
        if (indexToRemove > -1) this.brand[target].splice(indexToRemove, 1);
        if (this.brand_uploads[target])
          this.brand_uploads[target]?.removeFileById?.(delkey);

        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: `File has been removed "${file.name}".`,
        });
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: `Error while deleting ${file.name}.`,
        });
      }
    },

    deleteColorCard(colorType) {
      if (colorType) delete this.brand.colors[colorType];
    },

    addNewColor() {
      this.editing = true;
      const _ = this.reactiveDisplayBrand.colors || {};
      let num = 0;
      const prefix = 'ANewColor';
      for (const k of Object.keys(_)) {
        if (k.startsWith(prefix)) {
          let n = k.split(prefix)[1];
          if (n && !isNaN(n)) {
            num = Number(n) + 1;
          }
        }
      }
      _[`${prefix}${num}`] = '';
    },

    removeGoogleFont(font) {
      if (!this.editing) this.editing = true;
      if (!font || !this.brand.googlefonts) return;
      const fontIndex = this.brand.googlefonts.findIndex((e) => e === font);
      if (fontIndex > -1) {
        this.brand.googlefonts.splice(fontIndex, 1);
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: `Font has been removed "${font}".`,
        });
      }
    },

    factoryFn(files, target_path) {
      return new Promise((resolve, reject) => {
        const token = fnStore.getAuth();
        resolve({
          method: 'POST',
          fieldName: 'uploads[0]',
          url: `${this.$config?.api?.baseURL}/organization/${this.selectedOrg?.id}/brands/${this.brand_id}/upload`,
          formFields: [{ name: 'target_path', value: target_path }],
          headers: [{ name: 'Authorization', value: `Bearer ${token}` }],
          withCredentials: false,
        });
      });
    },

    addTempSkeleton(files, target) {
      if (!this.brand[target]) return;
      files.forEach((file) => {
        this.brand[target].unshift?.({
          file_info: file,
          isSkeleton: true,
          name: file.name,
          src: file.__img?.src,
        });
      });
    },

    uploadedEvent({ files, xhr }, target) {
      if (!this.brand[target]) return;
      const response = JSON.parse(xhr.response || '{}');
      if (response.success) {
        files.forEach((file, i) => {
          const file_info = response.data[i];
          if (file_info?.id) {
            const uploadedTempIndex = this.brand[target].findIndex(
              (e) => e.isSkeleton && e.file_info?.__key === file.__key
            );
            this.brand[target].splice(uploadedTempIndex, 1, file_info);
            // Update Brand
            this.addUploadToTargetBrand(this.brand[target], target);
          }
        });
      }
    },

    failedEvent({ files, xhr }, target) {
      if (!this.brand[target]) return;
      files.forEach((file) => {
        const indexToRename = this.brand[target].findIndex(
          (e) => e.file_info?.__key === file.__key
        );
        if (indexToRename > -1) this.brand[target][indexToRename].failed = true;
      });
    },

    async updateBrandAvatarAction(file, notify = true) {
      const response = await this.updateBrandAvatar(file?.id, this.brand_id);
      const message = extractErrorMessageString(response?.message);
      if (response?.success) {
        if (message) {
          if (notify)
            this.$q.notify({
              icon: 'icon-check-circle-broken',
              message: message || 'Brand logo has been updated',
            });
        }
        return true;
      } else {
        if (notify)
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: message || 'Error while processing your request',
          });
      }
    },

    fileRenameUpdate(fileInfo, target) {
      if (!this.brand[target]) return;
      const indexToRename = this.brand[target].findIndex(
        (e) => e.id == fileInfo.id
      );
      if (indexToRename > -1) this.brand[target][indexToRename] = fileInfo;
    },

    updateLightBox() {
      setTimeout(() => {
        this.lightboxImages = [];
        const lightboxElems = document.querySelectorAll('.with-lightbox');
        lightboxElems.forEach((e, i) => {
          let img = e.querySelector('img');
          if (img) {
            this.lightboxImages.push({
              src: img.src,
              title: e.getAttribute('file-name'),
              alt: e.getAttribute('file-name'),
            });
            e.onclick = () => {
              this.lightboxIndex = i;
              this.showLightBox = true;
            };
          }
        });
      }, 500);
    },

    addedImgColorSource(files) {
      files.forEach((file, i) => {
        if (i === files.length - 1) {
          this.$q.notify({
            spinner: true,
            message: `Getting colors from ${file.name}.`,
            timeout: 500,
          });
          this.brandColorSrcBlob = file?.__img?.src;
          this.brandColorSrcFile = file;
        }
      });
    },

    async getColorPalette() {
      let retry = 0;
      let result = this.getColorPaletteFn();
      while (!result && retry <= 5) {
        result = this.getColorPaletteFn();
        retry++;
      }
    },

    async getColorPaletteFn() {
      try {
        var imageColorThief = new ColorThief();
        let sourceImage = await getImageFromBase64str(this.brandColorSrc);
        if (sourceImage) {
          let colorPalettes = imageColorThief.getPalette(sourceImage, 100);
          if (!colorPalettes) return;
          this.colorPalette = colorPalettes.map((e) => `rgb(${e.join(',')})`);
        }
        return true;
      } catch (e) {
        return false;
      }
    },
  },
});
</script>
