<template>
  <p v-for="(str, i) in strList" :key="`str-${i}`">
    {{ str }}
  </p>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'DisplayString',

  props: {
    str: {
      type: String,
      default: '',
    },
  },

  computed: {
    strList() {
      return this.str.split('\n').filter((e) => e);
    },
  },
});
</script>
